}

p
{
    line-height: 1.5;
    font-size: 1.2rem;
    font-family: 'Arial', 'Helvetica Neue', Helvetica, sans-serif;
    color: #3f3f3f;
    margin-bottom: 15px;
}

.header-class
{
    font-family: Georgia, Times, serif;
    text-align: center;
    background-image: linear-gradient(to right, rgba(30, 47, 126, 0.3), rgba(109, 179, 242, .6)), url(/static/media/noisy_net.3f088219.png);
    color: #ffffff;
    font-size: 30px;
    border-bottom: 3px solid #666666;
}

.nav-link
{
    font-family:  sans-serif;
    font-size:    20px;
}

.nav-title
{
    height: 100%;
    font-family: sans-serif;
    font-size: 30px;
    color: #ffffff;
}

.nav-title:hover,
.nav-link:hover
{
    background-color:  rgba(30, 47, 126, 1);
    border-radius: 5px;
}
footer
{
    flex-shrink: 0;
    border-top: 1px solid #666666;
    background-image: linear-gradient(to right, rgba(109, 179, 242, .6), rgba(30, 47, 126, 0.3)), url(/static/media/noisy_net.3f088219.png);
    background-color: rgba(30, 47, 126, 0.3);
    color: #ffffff;
}

.google-logo
{
    background-image: url(/static/media/google.1a2eb573.png);
    background-size: 100px 33px;
    background-repeat: no-repeat;
    width: 108px;
    height: 33px;
}

.navbar
{
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
}
  
.navbar__link
{
    margin: 1em;
    font-size: 1.2em;
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
    text-decoration: none;
    letter-spacing: .3em;
    text-indent: .3em;
    color: #181818;
    border-bottom: 3px solid transparent;
}
  
.navbar__link:hover
{
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}
  
.navbar__link--active
{
    border-bottom: 3px solid #29b6f6;
    transition: border-bottom .5s ease-in-out;
}
  
